Meteor.methods({
    createPerson: function(args) {
        //æ ¹æ®èº«ä»½è¯å·ç åˆ¤å®šæ˜¯å¦åŒä¸€ä¸ªç”¨æˆ·

        var p = Persons.findOne({ no: args.no });
        if (p) {

            //æ›´æ–°å†…å®¹,ä¸ä¸ºç©ºåˆ™æ›´æ–°
            var modify = {};
            if(args.name){
                modify.name = args.name
            }
            if(args.address){
                modify.address = args.address
            }
            if(args.blood){
                modify.blood = args.blood
            }
            if(args.chronic){
                modify.chronic = args.chronic
            }
            if(args.other){
                modify.other = args.other
            }


            //å¦‚æžœæ¡£æ¡ˆå·²ç»å­˜åœ¨åˆ™åŠ å…¥å¯è®¿é—®åˆ—è¡¨
            Persons.update({ _id: p._id }, {
                $addToSet: {
                    users: args.userid
                },
                $set:modify
            });


        } else {
            //ä¸å­˜åœ¨åˆ™å»ºç«‹æ–°çš„æ¡£æ¡ˆ
            var obj = {
                name: args.name,
                sex: args.sex,
                brithday: args.brithday,
                no: args.no,
                address: args.address,
                tel: args.tel,
                urgent: args.urgent,
                blood: args.blood,
                rh: args.rh,
                chronic: args.chronic,
                allergy: args.allergy,
                other: args.other,
                users: [args.userid],
                nickname: args.nickname,
                avatar: args.avatar,
                createAt: new Date(),
                status: 1,
                updateAt: new Date(),
            };
            Persons.insert(obj);
        }

    },

});
